import React, { useState, useEffect } from "react";
import { Redirect } from "react-router-dom";
import "./mensajes.css";
//import io from "socket.io-client";

import { todosLosMensajes, chats } from "../Emulador";

export default (props) => {
  if (!props.sesion) return <Redirect to="/ClickArt" />;

  const [contacto, setcontacto] = useState("");
  return (
    <div className="contenedor">
      <Chat contacto={contacto} sesion={props.sesion} />
    </div>
  );
};

const Chat = (props) => {
  const [mensajes, setMensajes] = useState();
  const [mensaje, setMensaje] = useState("");

  //const socket = io("localhost:8888");

  useEffect(() => {
    setMensajes(todosLosMensajes(props.sesion[0], "Armando"));
  }, [chats]);

  const receptor = "Armando";
  const enviarMensaje = (e, mensaje) => {
    e.preventDefault();
    if (mensaje === "" || mensaje === " ") return;
    let id = 0;
    for (let i = 0; i < chats.length; i++) {
      if (id < chats[i].ID_Mensaje) {
        id = chats[i].ID_Mensaje;
      }
    }
    id++;
    chats.push({
      usuario: props.sesion[0],
      ID_Mensaje: id,
      mensaje: mensaje,
      receptor: receptor,
    });

    setMensajes(todosLosMensajes(props.sesion[0], "Armando"));
    setMensaje("");
  };

  const handleEnter = (event) => {
    if (event.key === "Enter") {
      enviarMensaje(event, mensaje);
    }
  };
  const handleCambio = (event, set) => set(event.target.value);
  return (
    <div className="chat">
      <div className="titulo-chat">
        <p>{props.contacto}Hernan</p>
      </div>
      <div className="caja-chat">
        {mensajes &&
          mensajes.map((mensaje, index) => {
            if (mensaje.usuario === props.sesion[0]) {
              return (
                <div
                  className="mensaje-chat mensajes-usuario"
                  key={"a" + index}
                >
                  <p key={index}>{mensaje.mensaje}</p>
                </div>
              );
            }
            return (
              <div className="mensaje-chat mensajes-contacto" key={"a" + index}>
                <p key={index}>{mensaje.mensaje}</p>
              </div>
            );
          })}
      </div>
      <div className="caja-de-mensaje-por-enviar">
        <textarea
          className="mensaje-por-enviar"
          onKeyDown={(event) => handleEnter(event)}
          onChange={(event) => handleCambio(event, setMensaje)}
          value={mensaje}
        />

        <button
          className="boton-envio"
          onClick={(e) => enviarMensaje(e, mensaje)}
        >
          Enviar
        </button>
      </div>
    </div>
  );
};
